:root {
  --primary-color: #2f9e44;
  --light-color: #44a857;
  --light-color-tint--1: #59b169;
  --dense-color: #267e36;
  --primary-text-color: #ececec;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  color: var(--primary-text-color);
  overscroll-behavior-y: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  line-height: 1;
}

.container {
  max-width: 76rem;
  margin: 0 auto;
}

.container-sm {
  max-width: 56rem;
  margin: 0 auto;
}

.btn {
  color: var(--primary-text-color);
  background-color: #0000;
  border: 2px solid;
  border-radius: 5px;
  align-items: center;
  gap: 2px;
  padding: .8rem;
  font-size: 1.4rem;
  font-weight: 500;
  transition: all .3s;
  display: flex;
}

.btn:hover {
  cursor: pointer;
  background-color: var(--light-color-tint--1);
  color: #fff;
}

.section--sep-line {
  border-bottom: 1px solid var(--dense-color);
  margin-top: 1.6rem;
}

.margin-bottom-sm {
  margin-bottom: 1.2rem;
}

.margin-bottom-md {
  margin-bottom: 1.6rem;
}

.margin-bottom-lg {
  margin-bottom: 2.4rem;
}

.border-bottom {
  border-bottom: 1px solid #c84242;
  width: 20px;
  margin-top: 3px;
}

.bold-text {
  font-weight: 600;
}

